a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

.login_button__33772 {
	border-radius: 20px;
	border: 1px solid white;
	background-color: #1d1e22;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}

button:active {
	transform: scale(0.95);
}

button:focus {
	outline: none;
}

button.login_ghost__yy6R8 {
	background-color: transparent;
	border-color: #FFFFFF;
}

.login_form__LyW62 {
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

.login_input__blz_- {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.login_container__zwwuV{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Montserrat', sans-serif;
	height: 100vh;
}

.login_cont__2uGln {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 768px;
	max-width: 100%;
	min-height: 480px;
}

.login_form-container__4DuRA {
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.login_sign-in-container__1qvTZ {
	left: 0;
	width: 50%;
	z-index: 2;
}

.login_cont__2uGln.login_right-panel-active__-rgSx .login_sign-in-container__1qvTZ {
	transform: translateX(100%);
}

.login_sign-up-container__7FWhf {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.login_cont__2uGln.login_right-panel-active__-rgSx .login_sign-up-container__7FWhf {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	-webkit-animation: login_show__26vY4 0.6s;
	        animation: login_show__26vY4 0.6s;
}

@-webkit-keyframes login_show__26vY4 {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}
	
	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

@keyframes login_show__26vY4 {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}
	
	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

.login_overlay-container__3MsSI {
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.login_cont__2uGln.login_right-panel-active__-rgSx .login_overlay-container__3MsSI{
	transform: translateX(-100%);
}

.login_overlay__3AjuQ {
	background: #FF416C;
	background: linear-gradient(to right, #1d1e22, #393f4d);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
  	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.login_cont__2uGln.login_right-panel-active__-rgSx .login_overlay__3AjuQ {
  	transform: translateX(50%);
}

.login_overlay-panel__3JP82 {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.login_overlay-left__3dciL {
	transform: translateX(-20%);
}

.login_cont__2uGln.login_right-panel-active__-rgSx .login_overlay-left__3dciL {
	transform: translateX(0);
}

.login_overlay-right__5Efit {
	right: 0;
	transform: translateX(0);
}

.login_cont__2uGln.login_right-panel-active__-rgSx .login_overlay-right__5Efit {
	transform: translateX(20%);
}


